import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import SnackbarContent from "@mui/material/SnackbarContent";

const dispatchedEvents = [];

const SrsMuiSnackbarContent = function (props, ref) {
  return (
    <SnackbarContent
      {...props}
      message={props.message}
      action={props.action}
      role={props.role}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiSnackbarContent component */
`;

const props = [
  {
    name: "message",
    defaultValue: "",
    type: "string",
  },
  {
    name: "action",
    defaultValue: null,
    type: "string",
  },
  {
    name: "role",
    defaultValue: "alert",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to SnackbarContent if needed
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSnackbarContent),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
